'use strict';

var $ = require('jquery');

require('wowjs');

var wow = new WOW( {
    boxClass:     'wow',      // default
    animateClass: 'animated', // default
    offset:       100,          // default
    mobile:       true,       // default
    live:         true        // default
    }
);

wow.init();

var lastScroll = 0;

if( $('.map').length ) {
    var map = $('.map-mask');
    var mapHeight = map.height();
    var mapOffset = map.offset().top - map.height();

    var banners = $('.experiences');
    var bannersOffset = banners.offset().top;
}

$(document).ready(function(){
    mapOffset = map.offset().top - map.height();
    bannersOffset = banners.offset().top;
});

$(document).on('scroll', function(){
    var scrollTop = $(document).scrollTop();
    // if( scrollTop > lastScroll && ! $('.menu-toggle').hasClass('active') ) {
    //     $('.header').addClass('hide');
    //     $('.menu-toggle').addClass('hide');
    // } else {
    //     $('.header').removeClass('hide');
    //     $('.menu-toggle').removeClass('hide');
    // }
    // if( scrollTop <= 0 ) {
    //     $('.header').removeClass('hide');
    //     $('.menu-toggle').removeClass('hide');
    // }

    if( $('.map').length ) {
        if( scrollTop > mapOffset ) {      
            if( scrollTop < bannersOffset ) {
                $('.map').removeClass('hide');
                if( scrollTop > mapOffset + mapHeight ) {
                    $('.header').addClass('hide');
                } else {
                    $('.header').removeClass('hide');
                }
            } else {
                $('.map').addClass('hide');
                $('.header').removeClass('hide');
            }
        } else {
            $('.map').addClass('hide');
            $('.header').removeClass('hide');
        }
    }
        
    lastScroll = scrollTop;
});

$('.menu-toggle').on('click tap', function(){
    $('.nav').toggleClass('active');
    $(this).toggleClass('active');
    if( $(window).width() < 768 ) {
        $('body').toggleClass('w-banners');
    }
});

