window.initMap = function initMap() {
  
  var map = new google.maps.Map(document.getElementById('map'), {
    zoom: 16,
    disableDefaultUI: true,
    mapTypeControl: false
  });
  
  map.setOptions({styles: mapStyle})
  
  if (navigator.geolocation) {
    var pos = {
        lat: -23.60722,
        lng: -46.68806
    }
    map.setCenter(pos);
    navigator.geolocation.getCurrentPosition(function(position) {
      var pos = {
        lat: position.coords.latitude,
        lng: position.coords.longitude
      };
      map.setCenter(pos);

      var geocodingAPI = "https://maps.googleapis.com/maps/api/geocode/json?latlng=" + pos.lat + "," + pos.lng + "&key=AIzaSyCyFHuXrJetzujrDX63nUqcfFTBcu9YoMU";
  
      $.getJSON(geocodingAPI, function (json) { // for location
        if (json.status == "OK") {
          //Check result 0
          var result = json.results[0];
          //look for locality tag and administrative_area_level_1
          var neighborhood = "";
          var state = "";
          var country = "";
          for (var i = 0, len = result.address_components.length; i < len; i++) {
            var ac = result.address_components[i];
            if (ac.types.indexOf("administrative_area_level_1") >= 0) {
              state = ac.short_name;
            }
            if (ac.types.indexOf("sublocality_level_1") >= 0) {
              neighborhood = ac.long_name;
            }
            if (ac.types.indexOf("country") >= 0) {
              country = ac.long_name;
            }
          }
          $('#neighborhood').html(neighborhood);
          $('#state').html(state);
          $('#country').html(country);
        }
      });

      var weatherJson = "http://api.openweathermap.org/data/2.5/weather?lat=" + pos.lat + "&lon=" + pos.lng + "&appid=e2f4720a724e820e64586889db9a99a3&units=metric";
    
      $.getJSON(weatherJson, function (data) { // for location
        $('#weather').html(data.main.temp.toFixed(0));
      });

    });
  }
};

