require('parsleyjs');

$('#contact-form').parsley({
  trigger: 'keydown'
});

$('#contact-form').on('submit', function() {
  $('.footer-form-loading').addClass('active');
  var type = $('.footer-form-select').val().toLowerCase();
  var mail = '';
  
  switch (type) {
    case 'a publisher':
      mail = "lkosaka@loganmedia.mobi";
      break;
    case 'an advertiser':
      mail = "iquintana@loganmedia.mobi";
      break;
    case 'a developer':
      mail = "sreingold@loganmedia.mobi";
      break;
    case 'other':
      mail = "contact@loganmedia.mobi";
      break;
    default:
      mail = "contact@loganmedia.mobi";
      break;
  }

  $.ajax({
    url: 'https://formspree.io/' + mail,
    data: $('#contact-form').serialize(),
    dataType: 'json',
    type: 'post',
  }).done(function(){
    $('.footer-form-loading').removeClass('active');
    $('.footer-form-success').addClass('active');
  });

  return false;

});